import React from "react";
import { GatsbyImage } from "gatsby-plugin-image";

export default function Figure({id, cn, caption, img, alt}) {
    return (
        <figure id={id} className={cn}>
            <GatsbyImage image={img.childImageSharp.gatsbyImageData} alt={alt}/>
            {caption && <figcaption>
                    {caption}
                </figcaption>}
        </figure>
    )
}