import React from "react";
import { graphql } from "gatsby";
import { GatsbyImage } from "gatsby-plugin-image";
import CaseStudyLayout from "../../components/case-studies/caseStudyLayout";
import CaseStudySection from "../../components/case-studies/caseStudySection";
import RoleImage from "../../images/page-decoration/Role.png";
import TeamImage from "../../images/page-decoration/Team.png";
import TextHeader from "../../components/case-studies/sectionTextHeader";
import SectionHeader from "../../components/case-studies/sectionHeader";
import Figure from "../../components/case-studies/figure";
import {
  CartPage,
  ProductPage,
  CandlesPages,
  ScentAndFamilyPages,
} from "../../components/case-studies/diptyque/final-design-sections";
import "../../styles/partials/_csDiptyque.scss";

export default function DiptyquePage({ data }) {
  const study = data.allMarkdownRemark.edges[0].node;
  const frontmatter = study.frontmatter;

  const project = frontmatter.diptyque;
  const overview = {
    role: frontmatter.role,
    team: frontmatter.team,
    project_goal: frontmatter.project_goal,
    project_description: frontmatter.project_description,
    tools: frontmatter.tools,
  };

  const conclusion = frontmatter.conclusion;
  const sections = project.hi_fi_section.sections;

  const wireframeImages = project.initial_design_concepts.wireframes.wireframes;
  const sketchImages = project.initial_design_concepts.sketching.sketches;
  const referencesImages =
    project.initial_design_concepts.sketching.supporting_reference_images;

  const cartPageSection = sections[0].section;
  const candlesPageSection = sections[1].section;
  const collectionAndFamilySection = sections[2].section;
  const productPageSection = sections[3].section;
  // const navigationSection = sections[4].section;
  // const creativeElements

  function ListItems({ arr, title }) {
    let list = arr;
    const cn = title + "-list";
    const licn = cn + "-item";
    return (
      <ul id="item-list-fn" className={`${cn}`}>
        {list.map(function (item, i) {
          return (
            <li
              key={i + "list-item" + title}
              id="list-item-fn"
              className={licn}
            >
              <span>{item}</span>
            </li>
          );
        })}
      </ul>
    );
  }

  return (
    <CaseStudyLayout frontmatter={frontmatter}>
      <CaseStudySection id={"diptyque-overview"} cn={"case-study-section"}>
        <SectionHeader title="Overview" />
        <div className="case-study-info project-info">
          <div className="about-project centered-8">
            <TextHeader title="Redesigning Diptyque" subtitle="" />
            <p className="article-text project-description">
              {overview.project_description}
            </p>
          </div>
          <div className="grid-text-items">
            <div id="problem-statement" className="titled-text-block">
              <TextHeader title="Problem" subtitle="" />
              <p className="article-text">{overview.project_goal.problem}</p>
            </div>
            <div id="solution-statement" className="titled-text-block">
              <TextHeader title="Goals" subtitle="" />
              <p className="article-text">{overview.project_goal.solution}</p>
            </div>
          </div>
        </div>
        <div className="case-study-info detail-items-info">
          <div className="grid-text-items">
            <div id="role">
              <img
                id="role-img"
                className="text-decoration-image"
                src={RoleImage}
                alt={`Role - ${overview.role}`}
              />
            </div>
            <div id="tools">
              <h4 className="text-title">Tools</h4>
              <ListItems title="tools" arr={overview.tools} />
            </div>

            <div id="team">
              <img
                id="team-img"
                className="text-decoration-image"
                src={TeamImage}
                alt={`Team - ${overview.team}`}
              />
            </div>
          </div>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection
        id={"diptyque-understanding"}
        cn={"case-study-section text-section"}
      >
        <SectionHeader title="Understanding" />

        <div className="user-research">
          <div className="text-title-container">
            <TextHeader
              title="User Research"
              subtitle="Identifying current pain points and what users want and need"
            />
            <div>
              <p className="article-text user-research-text">
                {project.preliminary_research.user_interviews}
              </p>
            </div>
          </div>
        </div>

        <div className="user-quotes">
          <ul className="user-quotes-text">
            {project.preliminary_research.user_quotes.map(function (item, i) {
              return (
                <li key={i + "user-quote"} className="user-quote">
                  <q>{item}</q>
                </li>
              );
            })}
          </ul>
        </div>

        <div className="feedback">
          <TextHeader
            title="User Feedback"
            subtitle="Synthesized perceptions and perspectives"
          />
          {project.preliminary_research.user_feedback.map(function (item) {
            return <p className="article-text">{item}</p>;
          })}
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection
        id={"diptyque-market-understanding"}
        cn={"case-study-section text-section"}
      >
        <SectionHeader title={project.market_understanding.title} />

        <div className="competitive-analysis">
          <div className="sub-section-text centered-16">
            <TextHeader
              title={project.market_understanding.competitive_analysis.title}
              subtitle={
                project.market_understanding.competitive_analysis.subtitle
              }
            />
            <p className="article-text">
              {project.market_understanding.competitive_analysis.text}
            </p>
          </div>
          <div className="images competitors-large-screens">
            <Figure
              img={project.market_understanding.competitive_analysis.visuals[0]}
              caption={"DS. & Durga"}
              id={"ds-durga"}
              cn={""}
              alt={"A screenshot of D.S & Durga's desktop website"}
            />

            <Figure
              img={project.market_understanding.competitive_analysis.visuals[1]}
              caption={"Le Labo"}
              id={"le-labo"}
              cn={""}
              alt={"A screenshot of Le Labo's desktop website"}
            />

            <Figure
              img={project.market_understanding.competitive_analysis.visuals[2]}
              caption={"Byredo"}
              id={"byredo"}
              cn={""}
              alt={"A screenshot of Byredo's desktop website"}
            />

            <Figure
              img={project.market_understanding.competitive_analysis.visuals[3]}
              caption={"Boy Smells"}
              id={"boy-smells"}
              cn={""}
              alt={"A screenshot of Boy Smell's desktop website"}
            />
          </div>
        </div>

        <div className="research">
          <div id="demographic" className="sub-section-text centered-8">
            <TextHeader
              title={project.market_understanding.research.demographics.title}
              subtitle={
                project.market_understanding.research.demographics.subtitle
              }
            />
            <span id="demographics-quotes">
              <span>
                <q className="large-quote">
                  Audience is 36.73% male and 63.27% female. The largest age
                  group of visitors are 25 - 34 year olds
                </q>
                <span id="asterick"> &#42;</span>
              </span>

              <span>
                <span>&#42;</span>According to this{" "}
                <a href="https://www.similarweb.com/website/diptyqueparis.com/#demographics">
                  report
                </a>
              </span>
            </span>
          </div>
          <div id="history" className="sub-section-text">
            <TextHeader
              title={project.market_understanding.research.history.title}
              subtitle={""}
            />
            <p className="article-text">
              {project.market_understanding.research.history.text}
            </p>
          </div>
          <div id="navigation" className="sub-section-text">
            <TextHeader
              title={project.market_understanding.research.navigation.title}
              subtitle={""}
            />
            <p className="article-text">
              {project.market_understanding.research.navigation.text}
            </p>
          </div>
          <Figure
            img={project.market_understanding.research.navigation.img}
            alt="Notes on the different ways competitors have structured their headers"
            id={"supporting-sketch-headers"}
            cn={""}
            caption="Notes on the different structure and language of the headers of competitors"
          />
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection
        id={"diptyque-design-process"}
        cn={"case-study-section text-section"}
      >
        <SectionHeader title="Design Process" />

        <div id="design-inspo">
          <div className="sub-section-text centered-16">
            <TextHeader
              title="Taking Inspiration"
              subtitle="Historical art as a inspiration for modern interfaces "
            />
            <p className="article-text">
              The founders of Diptyque were inspired by the ancient world. I
              similarly took inspiration from roman steles and medieval
              diptychs. I decided to use the diptychs two sided styling as
              inspiration on how to divide the layout and where the drawers
              would open to
            </p>
          </div>
          <div className="images references">
            <figure id="stele">
              <GatsbyImage
                image={referencesImages[0].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{referencesImages[0].caption}</figcaption>
            </figure>
            <figure id="diptych">
              <GatsbyImage
                image={referencesImages[1].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>
                <a
                  href={
                    project.initial_design_concepts.sketching
                      .supporting_links[0]
                  }
                >
                  {referencesImages[1].caption}
                </a>
              </figcaption>
            </figure>
          </div>
        </div>

        <div id="sketches-design">
          <div className="sub-section-text centered-16">
            <TextHeader
              title={project.initial_design_concepts.sketching.title}
              subtitle={project.initial_design_concepts.sketching.subtitle}
            />
            <p className="article-text">
              {project.initial_design_concepts.sketching.text}
            </p>
          </div>
          <div className="images sketches">
            <figure id="sketch-1">
              <GatsbyImage
                image={sketchImages[0].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{sketchImages[0].caption}</figcaption>
            </figure>
            <figure id="sketch-2">
              <GatsbyImage
                image={sketchImages[1].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{sketchImages[1].caption}</figcaption>
            </figure>
            <figure id="sketch-3">
              <GatsbyImage
                image={sketchImages[2].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{sketchImages[2].caption}</figcaption>
            </figure>
          </div>
        </div>

        <div id="wireframes-design">
          <div className="sub-section-text centered-16">
            <TextHeader
              title={project.initial_design_concepts.wireframes.title}
              subtitle={project.initial_design_concepts.wireframes.subtitle}
            />

            <p className="article-text">
              {project.initial_design_concepts.wireframes.text}
            </p>
          </div>
          <div className="images wireframes">
            <figure id="candles-page">
              <GatsbyImage
                image={wireframeImages[0].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{wireframeImages[0].caption}</figcaption>
            </figure>
            <figure id="product-page">
              <GatsbyImage
                image={wireframeImages[1].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{wireframeImages[1].caption}</figcaption>
            </figure>
            <figure id="collection-family-page">
              <GatsbyImage
                image={wireframeImages[2].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{wireframeImages[2].caption}</figcaption>
            </figure>
            <figure id="cart-page">
              <GatsbyImage
                image={wireframeImages[3].img.childImageSharp.gatsbyImageData}
              />
              <figcaption>{wireframeImages[3].caption}</figcaption>
            </figure>
          </div>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection
        id={"diptyque-usability-study"}
        cn={"case-study-section"}
      >
        <SectionHeader title={project.research_study_details.title} />

        <div className="diptyque-study-details">
          <TextHeader title={"Study Details"} subtitle={""} />
          <p className="article-text">{project.research_study_details.text}</p>
        </div>

        <div className="diptyque-study-questions">
          <ul className="">
            {project.research_study_details.questions.map(function (
              question,
              i
            ) {
              return (
                <li key={i + "study-question"}>
                  <p>{question}</p>
                </li>
              );
            })}
          </ul>
        </div>

        <div id="result-description">
          <TextHeader title={"Study Results"} subtitle={""} />
          <p className="article-text">
            {project.research_study_details.result.text}
          </p>
        </div>
        <div className="diptyque-results">
          <ul className="result-text">
            {project.research_study_details.result.research_results.map(
              function (result, i) {
                return (
                  <li key={i + "study-results"}>
                    <p>{result}</p>
                  </li>
                );
              }
            )}
          </ul>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"diptyque-visual-design"} cn={"case-study-section"}>
        <SectionHeader title={project.visual_design.title} />

        <div id="color-and-fonts-container">
          <Figure
            img={project.visual_design.images[0].image}
            alt={"Colors and fonts of the website"}
            id={"fonts-colors"}
            caption={project.visual_design.images[0].caption}
          />
          <div className="sub-section-text">
            <TextHeader
              title="Colors and Fonts"
              subtitle={project.visual_design.subtitle}
            />
            <p id="color-design" className="article-text">
              {project.visual_design.colors}
            </p>
            <p id="font-design" className="article-text">
              {project.visual_design.fonts}
            </p>
          </div>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection
        id={"high-fidelity-interfaces"}
        cn={"case-study-section"}
      >
        <SectionHeader title={project.hi_fi_section.title} />
        <CandlesPages data={candlesPageSection} />
        <CartPage data={cartPageSection} />
        <ProductPage data={productPageSection} />
        <ScentAndFamilyPages data={collectionAndFamilySection} />
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"diptyque-challenges"} cn={"case-study-section"}>
        <SectionHeader title={project.key_challenges_constraints.title} />

        <p id="main-text" className="article-text centered-8">
          {project.key_challenges_constraints.main_text}
        </p>

        <div id="challenges-list-container" className="centered-8">
          <ul>
            {project.key_challenges_constraints.constraints_and_challenges.map(
              function (item, i) {
                return (
                  <li key={i + "challenge"}>
                    <p>{item}</p>
                  </li>
                );
              }
            )}
          </ul>
        </div>
      </CaseStudySection>

      <div className="large-empty-spacer-block"></div>

      <CaseStudySection id={"diptyque-conclusion"} cn={"case-study-section"}>
        <SectionHeader title={conclusion.title} />
        <div id="final-thoughts" className="sub-section-text centered-8">
          <TextHeader title={conclusion.final_thoughts.title} subtitle={""} />
          <p className="article-text">{conclusion.final_thoughts.text}</p>
        </div>
        <div id="next-steps-text" className="sub-section-text centered-8">
          <TextHeader title={conclusion.next_steps.title} subtitle={""} />
          <p>{conclusion.next_steps.text}</p>
          <ul>
            {conclusion.next_steps.next_steps_array.map(function (item, i) {
              return (
                <li key={i + "next-steps"}>
                  <p>{item}</p>
                </li>
              );
            })}
          </ul>
        </div>
      </CaseStudySection>
    </CaseStudyLayout>
  );
}

export const pageQuery = graphql`
  query MyQuery {
    allMarkdownRemark(filter: { frontmatter: { slug: { eq: "diptyque" } } }) {
      edges {
        node {
          frontmatter {
            title
            sections
            tools
            banner_image {
              absolutePath
              childImageSharp {
                gatsbyImageData
                fluid {
                  srcSetWebp
                }
              }
            }
            project_goal {
              problem
              solution
            }
            team
            role
            time_frame
            project_description
            has_password

            conclusion {
              final_thoughts {
                text
                title
              }
              title
              next_steps {
                next_steps_array
                text
                title
              }
            }

            diptyque {
              preliminary_research {
                user_interviews
                user_quotes
                user_feedback
              }
              market_understanding {
                title
                competitive_analysis {
                  title
                  subtitle
                  text
                  visuals {
                    childImageSharp {
                      gatsbyImageData
                      fluid {
                        srcSetWebp
                      }
                    }
                  }
                }
                research {
                  demographics {
                    title
                    subtitle
                    text
                  }
                  history {
                    title
                    text
                  }
                  navigation {
                    title
                    text
                    img {
                      childImageSharp {
                        gatsbyImageData
                        fluid {
                          srcSetWebp
                        }
                      }
                    }
                  }
                }
              }
              initial_design_concepts {
                sketching {
                  title
                  subtitle
                  text
                  supporting_links
                  supporting_reference_images {
                    img {
                      childImageSharp {
                        gatsbyImageData
                        fluid {
                          srcSetWebp
                        }
                      }
                    }
                  }
                  sketches {
                    img {
                      childImageSharp {
                        gatsbyImageData
                        fluid {
                          srcSetWebp
                        }
                      }
                    }
                    caption
                  }
                  supporting_reference_images {
                    img {
                      childImageSharp {
                        gatsbyImageData
                        fluid {
                          srcSetWebp
                        }
                      }
                    }
                    caption
                  }
                }
                wireframes {
                  title
                  subtitle
                  text
                  wireframes {
                    img {
                      childImageSharp {
                        gatsbyImageData
                        fluid {
                          srcSetWebp
                        }
                      }
                    }
                    caption
                  }
                }
              }
              key_challenges_constraints {
                title
                subtitle
                main_text
                constraints_and_challenges
              }
              research_study_details {
                title
                subtitle
                text
                questions
                result {
                  text
                  research_results
                }
              }
              visual_design {
                title
                subtitle
                colors
                fonts
                images {
                  image {
                    childImageSharp {
                      gatsbyImageData
                    }
                  }
                  caption
                }
              }
              hi_fi_section {
                title
                subtitle
                text
                sections {
                  section {
                    title
                    subtitle
                    text
                    visuals {
                      gifs {
                        publicURL
                      }
                      images {
                        img {
                          extension
                          childImageSharp {
                            gatsbyImageData
                            fluid {
                              srcSetWebp
                            }
                          }
                        }
                        caption
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`;
